




import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import * as Tone from 'tone'

@Component
export default class HzPlayer extends Vue {
  @Prop({ default:  440 }) public frequency!: number
  @Prop({ default:  1 }) public gain!: number
  @Prop({ default:  1 }) public Q!: number

  private noise = new Tone.Noise('pink')
  private noiseShaper = new Tone.Filter(440, 'bandpass')
  private gainNode = new Tone.Gain()

  public mounted () {
    this.noise.connect(this.noiseShaper)
    this.noiseShaper.connect(this.gainNode)
    this.gainNode.connect(Tone.Destination)

    this.gainNode.gain.value = this.gain
    this.noiseShaper.frequency.value = this.frequency
    this.noiseShaper.Q.value = this.Q

    this.noise.start()
  }

  public beforeDestroy () {
    this.noise.stop()
    this.noise.dispose()
    this.noiseShaper.dispose()
    this.gainNode.dispose()
  }

  @Watch('frequency')
  private onFreqChange (val: number) {
    this.noiseShaper.frequency.value = val
  }

  @Watch('gain')
  private onGainChange (val: number) {
    this.gainNode.gain.value = val
  }

  @Watch('Q')
  private onQChange (val: number) {
    this.noiseShaper.Q.value = val
  }
}
